import ajax from '@/services/ajax';
import api from '@/services/api';

export function getTestIndex(data) {
    return ajax.post(api.getTestIndex, data);
}


export function getAccountList(data) {
    return ajax.postAfterLoginQueryList('012020', data, api.getCardList);
    // return ajax.postAfterLoginQueryList( '012020', data, api.getTestIndex);
}

export function getAccountDetail(data) {
    return ajax.postAfterLoginQuery( '017403', data, api.getTestIndex);
}


