<template>
    <div>
        <infinite-scroll ref="infiniteScroll" :data="data" :hasMoreData="hasMoreData" @pullup="pullup" >
            <mt-cell v-for="item in data" :key="item.id" title="" :value="item.value" ></mt-cell>
        </infinite-scroll>
    </div>
</template>

<script>

import { mapGetters, mapActions } from 'vuex';

export default {
    data() {
        return {
            data: [],
            loadingMore: false,
            hasMoreData: true,
            count: 1
        };
    },
    created() {
        let _data = [];
        for(let i = 0; i < 10; i++) {
            _data.push({ id: i, value: +new Date() });
        }
        this.data = _data;
    },
    computed: {
    },
    components: {
    },
    methods: {
        pullup (){
            console.log('pullup');
            console.log(this.loadingMore);
            if( this.loadingMore ) {
                return;
            }
            if( this.count >= 3 ) {
                this.hasMoreData = false;
                return;
            }
            console.log('loadingMore');
            this.loadingMore = true;
            this.count ++;
            console.log(this.count);
            setTimeout( () => {
                let _data = [];
                for(let i = 0; i < 10; i++) {
                    _data.push({ id: i, value: '' + +new Date() });
                }
                this.data = this.data.concat( _data );
                this.loadingMore = false;
            }, 2000);
        }
    }
};
</script>


